import { create } from "zustand";
import { persist } from "zustand/middleware";

export interface WalletState {
  accountId: string | null;
  isConnected: boolean;
  connect: (accountId: string) => void;
  disconnect: () => void;
}

export interface AppState {
  wallet: WalletState;
  isOnline: boolean;
  setOnlineStatus: (status: boolean) => void;
}

export const useSimpleStore = create<AppState>()(
  persist(
    (set) => ({
      wallet: {
        accountId: null,
        isConnected: false,
        connect: (accountId: string) => {
          set((state) => ({
            wallet: { ...state.wallet, accountId, isConnected: true },
          }));
        },
        disconnect: () => {
          set((state) => ({
            wallet: { ...state.wallet, accountId: null, isConnected: false },
          }));
        },
      },
      isOnline: true,
      setOnlineStatus: (status: boolean) => set({ isOnline: status }),
    }),
    {
      name: "agriyield-simple-storage",
    }
  )
);
